h1, h2, h3, h4, h5, h6, p, a, body, li, ul {
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: Inter, sans-serif;
  src: url("Inter-Regular.30288fd8.ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Inter, sans-serif;
  src: url("Inter-Medium.57962c8a.ttf");
  font-weight: 500;
  font-style: normal;
}

* {
  box-sizing: border-box;
}

.page {
  scroll-behavior: smooth;
  font-family: Inter, sans-serif;
}

.page__body {
  background: #ffeefc;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page__body--with-menu {
  overflow: hidden;
}

.header {
  margin-bottom: 64px;
}

@media (width >= 640px) {
  .header {
    margin-bottom: 0;
  }
}

@media (width >= 1280px) {
  .header {
    margin-bottom: 152px;
  }
}

.header__top {
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  display: flex;
}

@media (width >= 1280px) {
  .header__top {
    padding: 28px 32px;
  }
}

.header__menu {
  display: none;
}

@media (width >= 1280px) {
  .header__menu {
    display: block;
  }
}

.header__logo {
  width: 136px;
  height: 32px;
}

.header__btn {
  background-image: url("Icon-menu.c83c559f.svg");
  width: 24px;
  height: 24px;
}

@media (width >= 1280px) {
  .header__btn {
    display: none;
  }
}

.header__container {
  padding-inline: 16px;
}

@media (width >= 640px) {
  .header__container {
    padding-inline: 44px;
  }
}

@media (width >= 1440px) {
  .header__container {
    padding-inline: 124px;
  }
}

.header__title {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 16px;
  display: grid;
}

@media (width >= 640px) {
  .header__title {
    --columns: 8;
    column-gap: 24px;
  }
}

.header__text {
  letter-spacing: -.01em;
  text-align: center;
  color: #1c1a26;
  grid-column: 1 / -1;
  margin-top: 32px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

@media (width >= 640px) {
  .header__text {
    text-align: left;
    grid-column: 1 / 4;
    margin-top: 80px;
  }
}

@media (width >= 1280px) {
  .header__text {
    grid-area: 1 / 1 / 2 / 3;
    align-self: flex-start;
    margin-top: 135px;
  }
}

@media (width >= 1440px) {
  .header__text {
    margin-top: 144px;
  }
}

.header__label {
  letter-spacing: -.05em;
  text-align: center;
  text-transform: uppercase;
  color: #f9512e;
  grid-column: 1 / -1;
  margin-top: 32px;
  font-size: 64px;
  font-weight: 400;
  line-height: 54.4px;
}

@media (width >= 640px) {
  .header__label {
    grid-row: 2 / 3;
    align-self: center;
    margin-top: 0;
    font-size: 80px;
    line-height: 68px;
  }
}

@media (width >= 1280px) {
  .header__label {
    grid-row: 1 / 2;
