}

.header__img {
  grid-column: 1 / -1;
  width: 100%;
  height: auto;
}

@media (width >= 640px) {
  .header__img {
    z-index: -1;
    grid-area: 2 / 3 / 3 / 7;
    margin-top: 16px;
  }
}

@media (width >= 1280px) {
  .header__img {
    grid-area: 1 / 3 / 2 / 7;
    margin-top: 0;
