}

.header__title__link {
  letter-spacing: .02em;
  text-align: left;
  text-transform: uppercase;
  color: #f9512e;
  border: 2px solid #f9512e;
  border-radius: 32px;
  grid-column: 1 / -1;
  justify-content: center;
  align-items: center;
  gap: 6px;
  padding-block: 20px;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s linear;
  display: flex;
}

@media (width >= 640px) {
