    align-self: flex-end;
    margin-top: 0;
    margin-bottom: 161px;
  }
}

.header__title__link:hover {
  color: #fff;
  background-color: #f9512e;
  box-shadow: 0 0 50px 5px #f9512e;
}

.header__title__link:focus {
  background-color: #b2341a;
}

.header__title__link:before {
  content: "";
  background-image: url("Icon-cookie.d3c9dd57.svg");
  width: 24px;
  height: 24px;
}

.header__title__link:hover:before {
  color: #fff;
  background-image: url("icon-cookie-hover.681032f5.svg");
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 64px;
  padding: 8px 16px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  letter-spacing: -.01em;
  text-align: left;
  color: #1c1a26;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-decoration: none;
  position: relative;
}

.nav__link:after {
  content: "";
  background-color: #f9512e;
  width: 0%;
  height: 2px;
  transition: all .3s ease-in-out;
  position: absolute;
  top: -5px;
  right: 5%;
}

.nav__link:hover:after {
  width: 100%;
  right: 0;
}

.nav__link:before {
  content: "";
  background-color: #f9512e;
  width: 0%;
  height: 2px;
  transition: all .3s ease-in-out;
  position: absolute;
  bottom: -5px;
  left: 5%;
}

.nav__link:hover:before {
  width: 100%;
  left: 0;
}

.nav__link--call {
  text-transform: uppercase;
  color: #f9512e;
  align-items: center;
  gap: 4px;
  display: flex;
}

.nav__phone {
  background-image: url("Icon-phone.524cfc2e.svg");
  width: 24px;
  height: 24px;
}

.menu {
  box-sizing: border-box;
  z-index: 1;
  background-color: #ffeefc;
  height: 100vh;
  overflow: hidden;
}

.menu__content {
